import { createGlobalStyle } from "styled-components";

const GlobalStyle = createGlobalStyle`

    *{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }
    
    html {
        /* Desktops and laptops ----------- */
        @media only screen and (max-width : 120em) {
        /* Styles */
        font-size: 75%;
    }
    
    }

    body{
        background: #1b1b1b;
        font-family: 'Inter', sans-serif;
        overflow-x: hidden;
    }

    button{
        font-weight: bold;
        font-size: 1.1.rem;
        cursor: pointer;
        padding: 1rem 2rem;
        border: 3px solid #23d997;
        background: transparent;
        color: #ffffff;
        transition: all .5s ease;
        font-family: 'Inter', sans-serif;
        &:hover{
            background-color: #23d997;    
        }
    }

    h2{
        font-weight: lighter;
        font-size: 4rem;
    }

    h3{
        color: #ffffff;
    }

    h4{
        font-weight: bold;
        font-size: 2rem;
    }

    span{
        font-weight: bold;
        color: #23d997;
    }

    a{
        font-size: 1.1rem;
    }

    p{
        padding: 3rem 0;
        color: #cccccc;
        font-size: 1.4rem;
        line-height: 150%;
    }
`;

export default GlobalStyle;
