import { motion } from "framer-motion";
import styled from "styled-components";

export const StyledAbout = styled(motion.div)`
  min-height: 90vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5rem 10rem;
  color: #ffffff;

  /* Desktops and laptops ----------- */
  @media only screen 
    and (max-width : 80em) {
    /* Styles */
      display: block;
      padding: 2rem 2rem;
      text-align: center;
    }
`;

export const StyledDescription = styled.div`
  flex: 1;
  padding-right: 5rem;
  z-index: 2;
  h2 {
    font-weight: lighter;
  }

  /* Desktops and laptops ----------- */
  @media only screen 
    and (max-width : 80em) {
    /* Styles */
      padding: 0;
      button {
        margin: 2rem 0 5rem;
      }
    }
`;

export const StyledImage = styled.div`
  flex: 1;
  overflow: hidden;
  z-index: 2;
  img {
    width: 100%;
    height: 80vh;
    object-fit: cover;
  }
`;

export const StyledHide = styled.div`
  overflow: hidden;
`